import { TrataErrorService } from 'services/trata-error.service';
import { CardapioDTO } from './../../../../../models/domain/cardapio.dto';
import { Route, ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';

import { AbstractCardapioComponent } from '../abstract-cardapio.component';
import { CardapioService } from './../cardapio.service';


@Component({
  templateUrl: './update-cardapio.component.html',
  styleUrls: ['./update-cardapio.component.scss']
})
export class UpdateCardapioComponent extends AbstractCardapioComponent implements OnInit {

  
  constructor(
    public fb: FormBuilder,
    public location: Location,
    public route: ActivatedRoute,
    public cardapioService: CardapioService) {
    super(fb, location, cardapioService);
  }

  ngOnInit() {
    this.criarFormulario();
    this.route.params.forEach((params: ParameterDecorator) => {
      let id = params['id'];
      this.cardapioService.findById(id).subscribe(
        resp => {
          this.cardapio = resp as CardapioDTO;
          this.alterarForm(this.cardapio);
        }
        , error => TrataErrorService.tratarError(error)
      )
    });

  }

}



