
import { NgModule, LOCALE_ID } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { SelectModule } from 'ng2-select';
import { ModalModule } from 'ng2-bootstrap/modal';
import { PopupModalModule } from 'app/shared/popup/popup-modal.module';
import { SharedComponentModule } from './../../../shared/shared-component.module';
import { TextMaskModule } from 'angular2-text-mask';
import { TabsModule } from 'ng2-bootstrap/tabs';

import { StorageService } from './../../../../services/storage.service';

import { CardapioService } from './cardapio.service';
import { CardapioRoutingModule } from './cardapio-routing.module';

import { ListarCardapioComponent } from './listar/listar-cardapio.component';
import { UpdateCardapioComponent } from './update/update-cardapio.component';
import { CreateCardapioComponent } from './create/create-cardapio.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModalModule.forRoot(),
    SharedComponentModule,
    ReactiveFormsModule,
    HttpClientModule,
    PopupModalModule,
    CardapioRoutingModule
  ],
  declarations: [
    ListarCardapioComponent,
    CreateCardapioComponent,
    UpdateCardapioComponent
  ],
  providers:[
    CardapioService,  
    StorageService
  ]
})
export class CardapioModule { }



