import { CardapioDTO } from './../../../../../models/domain/cardapio.dto';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { API_CONFIG } from 'config/core/api.config';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { TrataErrorService } from './../../../../../services/trata-error.service';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { CardapioService } from './../cardapio.service';

@Component({
  templateUrl: './listar-cardapio.component.html',
  styleUrls: ['./listar-cardapio.component.scss']
})
export class ListarCardapioComponent extends AbstractBaseComponent implements OnInit {

  @ViewChild('excluirModal') popupExclusao;
  @ViewChild('changeStatusModal') popupChangeStatus;

  cardarpio: CardapioDTO;
  items: CardapioDTO[] = [];

  constructor(public cardapioService: CardapioService, public router: Router) {
    super();
  }

  ngOnInit() {
    this.carregarTodos()
  }

  selecionarItem(item: CardapioDTO) {
    this.cardarpio = item;
    this.popupExclusao.show();
  }

  selecionarItemChangeStatus(item: CardapioDTO) {
    this.cardarpio = item;
    this.popupChangeStatus.show();
  }

  alterarCardapio(item: CardapioDTO) {
    this.router.navigate([`cardapios/editar/${item.id}`])
  }

  excluir() {
    this.cardapioService.excluir(this.cardarpio)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Cardapio', 'O Cardapio foi excluído com sucesso!!!')
        this.carregarTodos();
      }, error => TrataErrorService.tratarError(error));
  }

  changeStatus() {
    this.cardapioService.changeStatus(this.cardarpio)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Cardapio', 'Status do cardapio foi alterado com sucesso!!!')
        this.carregarTodos();
      }, error => TrataErrorService.tratarError(error));
  }


  private carregarTodos() {
    this.cardapioService.findAll().subscribe(
      res => this.items = res,
      error => TrataErrorService.tratarError(error));
  }

}



