import { ViewChild } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { Location } from '@angular/common';

import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { TrataErrorService } from './../../../../services/trata-error.service';
import { CardapioService } from './cardapio.service';
import { CardapioItemDTO } from './../../../../models/domain/cardapio-item.dto';
import { CardapioDTO } from './../../../../models/domain/cardapio.dto';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';



export abstract class AbstractCardapioComponent extends AbstractBaseComponent {

  @ViewChild('modalExcluirItemCardapio') popupExclusaoItem;
  @ViewChild('modalAlterarItemCardapio') popupAlterarItemCardapio;

  cardapio = {} as CardapioDTO;
  cardapioItem ={} as CardapioItemDTO;

  cardapioItens: CardapioItemDTO[] = [];


  constructor(
    public fb: FormBuilder,
    public location: Location,
    public cardapioService: CardapioService) {
    super();
  }



  protected confirmar(value: any) {
    this.cardapioService.salvar(value)
      .subscribe((response) => {
        if (response['body'] != null) {
          this.cardapio = JSON.parse(response['body']);
          this.alterarForm(this.cardapio);
        }
        //EventEmitterService.emitirMensagemToasterSucesso('Cardápio', 'O Cardapio foi incluído com sucesso!!!');
      }, error => TrataErrorService.tratarError(error));
  }


  protected criarFormulario() {
    this.formulario = this.fb.group({
      id: [],
      nmCardapio: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(40)]],
      dsComentario: [''],
      status: ['I'],
      emAlteracao: false
    })

  }

  protected alterarForm(cardapio: CardapioDTO) {
    this.formulario.patchValue({
      id: cardapio.id,
      nmCardapio: cardapio.nmCardapio,
      dsComentario: cardapio.dsComentario,
      status: cardapio.status,
      emAlteracao: true
    })
    this.cardapioItens = cardapio.itens
  }

  protected voltar() {
    this.location.back();
  }

  public excluirItemCardapio() {
    this.cardapioService.excluirItem(this.cardapioItem)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Cardapio', 'O item do cardapio foi excluído com sucesso!!!')
        this.carregarItemCardapio(this.cardapio.id)
      }, error => TrataErrorService.tratarError(error));

  }

  protected selecionarItem(item: CardapioItemDTO) {
    this.cardapioItem = item;
    this.popupExclusaoItem.show();
  }

  protected adicionarItemCardapio(value: any) {
    let item = {} as CardapioItemDTO;
    item.cardapio = this.cardapio;
    item.produto = value.produto;
    item.nrOrdemItem = value.nrOrdemItem;
    this.cardapioService.salvarItem(item)
      .subscribe(
        res => this.carregarItemCardapio(this.cardapio.id),
        error => TrataErrorService.tratarError(error));
  }

  protected carregarItemCardapio(idCardapio: string) {
    if (idCardapio != null) {
      this.cardapioService.findAllItemByCardapio(idCardapio).subscribe(
        res => this.cardapioItens = res,
        error => TrataErrorService.tratarError(error)
      )
    }
  }
  

  protected popupAlterarItem(item: CardapioItemDTO) {
    this.cardapioItem = item;
    this.popupAlterarItemCardapio.show();
  }

  protected salvarItem() {
    this.cardapioService.atualizarItem(this.cardapioItem)
      .subscribe(
        res => this.carregarItemCardapio(this.cardapio.id),
        error => TrataErrorService.tratarError(error)
      );

  }
  protected definirCor() {
    this.cardapioItem.destaque = !this.cardapioItem.destaque;
  }
}


