import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';

import { AbstractCardapioComponent } from '../abstract-cardapio.component';
import { CardapioService } from './../cardapio.service';


@Component({
  templateUrl: './create-cardapio.component.html',
  styleUrls: ['./create-cardapio.component.scss']
})
export class CreateCardapioComponent extends AbstractCardapioComponent implements OnInit {

  constructor(
    public fb: FormBuilder,
    public location: Location,
    public cardapioService: CardapioService) {
    super(fb, location, cardapioService);
  }


  ngOnInit() {
    this.criarFormulario();
  }

}



