import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ListarCardapioComponent } from './listar/listar-cardapio.component';
import { CreateCardapioComponent } from './create/create-cardapio.component';
import { UpdateCardapioComponent } from './update/update-cardapio.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'listar',
    pathMatch: 'full',
  },
  {
    path: '',
    data: {
      title: 'Cardapios'
    },
    children: [
      {
        path: 'listar',
        component: ListarCardapioComponent,
        data: {
          title: 'Listar'
        }
      },
      {
        path: 'novo',
        component: CreateCardapioComponent,
        data: {
          title: 'Novo'
        }
      },
      {
        path: 'editar/:id',
        component: UpdateCardapioComponent,
        data: {
          title: 'Alterar'
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CardapioRoutingModule { }



